import React, { useEffect, useState } from 'react';
import { SearchBar, Carousel } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import store2 from '../reduxDemo/collect';
import './Home.css';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
const header = {
  position: 'fixed',
  left: '0px',
  top: '0px',
  width: '100%',
  background: 'rgb(255, 133, 42)',
  zIndex: 1,
};
const searchContainer = {
  padding: '6px 9px 6px 42px',
  backgroundImage:
    'url("data:image/png;base64,")',
  backgroundRepeat: 'no-repeat',
  backgroundSize: '27px 20px',
  backgroundPosition: '9px center',
};
const searchBar = {
  display: 'block',
  background: 'white',
  borderRadius: '4px',
  height: '25px',
  lineHeight: '25px',
  textAlign: 'center',
  color: 'rgb(240, 224, 220)',
};
const para = {
  width: '30%',
  height: 'auto',
  marginTop: '3%',
  display: 'block',
  margin: '3% auto',
};

//创建容器组件.
const Box = ({ props }) => {
  const { img, price, title } = props;
  let history = useHistory();
  const onClick = () => {
    props.back = '/';
    history.push('/detail', props);
  };
  return (
    <div className="list-box" onClick={onClick}>
      <img src={img} alt={title} />
      <div className="info">
        <p className="title">{title}</p>
        <span className="price">￥&nbsp;&nbsp;{price}</span>
      </div>
    </div>
  );
};

const Home = (props) => {
  const [list, setList] = useState(); //用户存储反馈数据后的页面结构

  //请求商品数据
  const getData = () => {
    let store = store2.getState();

    if (store.data.length === 0) {
      //fetch 请求获取数据   参考:https://developer.mozilla.org/zh-CN/docs/Web/API/Fetch_API/Using_Fetch
      fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
        .then((res) => {
          return res.json(); //转换数据为json格式
        })
        .then((res) => {
          let newlist = []; //空数组用来放置
          res.data.forEach((e) => {
            newlist.push(<Box props={e} key={e.id} />); //循环处理放置box的列表中
          });
          setList(newlist); //更新列表的结构
          store2.dispatch({
            type: 'setData',
            new: res.data,
          });
        });
    } else {
      let newlist = []; //空数组用来放置
      store.data.forEach((e) => {
        newlist.push(<Box props={e} key={e.id} />); //循环处理放置box的列表中
      });
      setList(newlist); //更新列表的结构
    }
  };

  //用户初始化请求  参考:https://zh-hans.reactjs.org/docs/hooks-effect.html
  useEffect(() => {
    getData();
  }, []);

  return (
    <div>
      <header style={header}>
        <div style={searchContainer} onClick={() => props.history.push('/search')}>
          <SearchBar placeholder="寻找宝贝店铺" style={searchBar}></SearchBar>
        </div>
      </header>
      <Carousel autoplay infinite style={{ marginTop: '35px' }}>
        <img style={{ height: '100%' }} src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg" alt="" />
        <img style={{ height: '100%' }} src="//gw.alicdn.com/imgextra/i4/94/O1CN016EMBmd1CZ5rQwKF7U_!!94-0-lubanu.jpg" alt="" />
        <img style={{ height: '100%' }} src="//gw.alicdn.com/imgextra/i1/158/O1CN01LWnzwd1D2PD1NAcCh_!!158-0-lubanu.jpg" alt="" />
        <img style={{ height: '100%' }} src="//gw.alicdn.com/imgextra/i2/104/O1CN010jvtis1Cdfp5OSQOA_!!104-0-lubanu.jpg" alt="" />
        <img
          style={{ height: '100%' }}
          src="https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000003837/O1CN016az1BO1eDOKoVKRJ4_!!6000000003837-0-octopus.jpg"
          alt=""
        />
      </Carousel>
      <div>
        <img style={para} src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png" alt=""></img>
      </div>

      {/* 这里加载列表的dom */}
      <div className="list-container">{list}</div>
    </div>
  );
};

export default Home;
