import { createStore } from 'redux';

const f = (state, action) => {
  switch (action.type) {
    case 'setData':
      state.data = action.new;
      return state;
    case 'setCollect':
      state.collect = action.new;
      return state;
    case 'setUser':
      state.userinfo = action.new;
      return state;
    default:
      return {
        data: [],
        collect: new Map(),
        userinfo: '',
      };

  }
};

const store2 = createStore(f);

export default store2;
