import { TabBar } from 'antd-mobile';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import store2 from '../reduxDemo/collect';
import './Detail.css';

const Detail = () => {
  const history = useHistory();
  const state = history.location.state;
  let store = store2.getState();
  const [isCollect, setIsCollect] = useState(false);

  useEffect(() => {
    getCollect();
  }, []);

  const back = () => {
    history.push(state.back);
  };

  const collect = () => {
    if (isCollect) {
      store.collect.delete(state.id);
      setIsCollect(false);
    } else {
      store.collect.set(state.id, state);
      setIsCollect(true);
    }
  };

  const getCollect = () => {
    setIsCollect(store.collect.get(state.id) !== undefined);
  };
  return (
    <div className="detail">
      <div className="back" onClick={back}>
        <span className="back-icon">{'<'}</span>
      </div>
      <div className="img">
        <img src={state.img}></img>
      </div>
      <div className="info">
        <div className="price">{state.price}</div>
        <div className="title">{state.title}</div>
        <div className="subinfo">
          <div>快递: 10.00元</div>
          <div style={{ textAlign: 'center' }}>月销500+</div>
          <div style={{ textAlign: 'end' }}>浙江杭州</div>
        </div>
      </div>
      <div className="gap"></div>

      <div className="bottom">
        <div className="left">
          <TabBar tintColor={'#ff5000'}>
            <TabBar.Item 
              icon={<i className="iconfont icon-dianpu1"></i>}
              selectedIcon={<i className="iconfont icon-dianpu1"></i>}
              title="店铺" 
              key="店铺">
            </TabBar.Item>
            <TabBar.Item 
              icon={<i className="iconfont icon-kefu1"></i>}
              selectedIcon={<i className="iconfont icon-kefu1"></i>} 
              title="客服" 
              key="客服">
            </TabBar.Item>
            <TabBar.Item 
              icon={<i className="iconfont icon-Star"></i>}
              selectedIcon={<i className="iconfont icon-Star-copy"></i>} 
              title="收藏" 
              key="收藏" 
              selected={isCollect} 
              onPress={collect}>
            </TabBar.Item>
          </TabBar>
        </div>
        <div className="right">
          <button className="addTo">加入购物车</button>
          <button className="buyNow">立即购买</button>
        </div>
      </div>
    </div>
  );
};

export default Detail;
