import React, { useEffect, useState } from 'react';
import store2 from '../reduxDemo/collect';
import { useHistory } from 'react-router-dom';
import { Icon } from 'antd-mobile';
import './Cart.css';

//创建容器组件.
const Box = ({ props, getCollect }) => {
  const { img, price, title } = props;
  let history = useHistory();
  let store = store2.getState();
  const onClick = (event) => {
    let tag = event.target.tagName;
    if (tag === 'use' || tag === 'svg') {
      store.collect.delete(props.id);
      getCollect();
    } else {
      props.back = '/cart';
      history.push('/detail', props);
    }
  };

  return (
    <>
      <div className="box" onClick={onClick}>
        <Icon type="cross-circle" size="s" className="close" />
        <img src={img} alt={title} />
        <div className="info">
          <p className="title">{title}</p>
          <span className="price">￥&nbsp;&nbsp;{price}</span>
        </div>
      </div>
    </>
  );
};

let getCollect;
const Cart = () => {
  let store = store2.getState();
  let history = useHistory();
  const [list, setList] = useState();

  getCollect = () => {
    if (store.userinfo === '') {
      history.push('/login', { back: 'cart' });
      return;
    }

    let newlist = []; //空数组用来放置
    store.collect.forEach((e) => {
      newlist.push(<Box props={e} key={e.id} getCollect={getCollect} />); //循环处理放置box的列表中
    });

    setList(newlist); //更新列表的结构
  };

  useEffect(() => {
    getCollect();
  }, []);

  return (
    <div className="cart">
      <div className="title">收藏列表</div>
      <div className="list">{list}</div>
    </div>
  );
};

export default Cart;
