import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }
  render() {
    const { pathname } = this.props.location;
    console.log(pathname.split('/'));
    if (pathname.split('/').length > 2 || pathname.startsWith('/detail')) {
      return null;
    }
    return (
      <div
        style={{
          position: 'fixed',
          width: '100%',
          bottom: 0,
        }}
      >
        <TabBar unselectedTintColor="#949494" tintColor="#ff5000" barTintColor="white">
          <TabBar.Item
            title="首页"
            key="home"
            icon={<i className="iconfont icon-shouye"></i>}
            selectedIcon={<i className="iconfont icon-shouye-copy"></i>}
            selected={pathname === '/'}
            onPress={() => {
              this.props.history.push('/');
            }}
            data-seed="logId"
          ></TabBar.Item>
          <TabBar.Item
            icon={<i className="iconfont icon-Star"></i>}
            selectedIcon={<i className="iconfont icon-Star-copy"></i>}
            title="收藏"
            key="cart"
            selected={pathname === '/cart'}
            onPress={() => {
              this.props.history.push('/cart');
            }}
            data-seed="logId1"
          ></TabBar.Item>
          <TabBar.Item
            icon={<i className="iconfont icon-dingdanliebiao"></i>}
            selectedIcon={<i className="iconfont icon-dingdanliebiao-copy"></i>}
            title="订单列表"
            key="Friend"
            selected={pathname === '/Order'}
            onPress={() => {
              this.props.history.push('/Order');
            }}
          ></TabBar.Item>
          <TabBar.Item
            icon={<i className="iconfont icon-wode2"></i>}
            selectedIcon={<i className="iconfont icon-wode2-copy"></i>}
            title="我的淘宝"
            key="my"
            selected={pathname === '/tab'}
            onPress={() => {
              this.props.history.push('/tab');
            }}
          ></TabBar.Item>
        </TabBar>
      </div>
    );
  }
}
export default withRouter(Mytab);
