import React, { useState } from 'react';
import { List, InputItem, Button, Toast } from 'antd-mobile';
import store2 from '../reduxDemo/collect';
import { useHistory } from 'react-router-dom';
import './Login.css';

const Login = () => {
  const Item = List.Item;
  const [user, setUser] = useState();
  const [pas, setPas] = useState();
  const history = useHistory();
  const state = history.location.state;

  const login = () => {
    fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login', {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        user,
        pas,
      }),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res.desc === 'success') {
          store2.dispatch({
            type: 'setUser',
            new: res.data.name,
          });
          try {
            history.push(state.back);
          } catch (error) {
            history.push('/');
          }
        } else {
          alert('登录失败');
        }
      });
  };

  return (
    <div className="login">
      <div className="title">您需要登录才能继续使用</div>
      <div className="container">
        <div className="logo"></div>
        <List>
          <div className="input">
            <InputItem placeholder="手机号/邮箱/会员名" clear onChange={setUser}></InputItem>
          </div>
          <div className="input">
            <InputItem placeholder="请输入登录密码" type="password" onChange={setPas}></InputItem>
          </div>
          <Item>
            <Button className="submit" onClick={login}>
              登录
            </Button>
          </Item>
        </List>
      </div>
    </div>
  );
};
export default Login;
